.overlay {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-color: rgba(65, 64, 64, 0.733);
  z-index: 99;
}
.modal {
  background-color: white;
  overflow: hidden;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.img {
  max-width: 95vw;
  max-height: 98vh;
  display: block;
}
